.div {
  background: pink;
  justify-content: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  position: sticky;
  top: 0;
  margin: 0px;
}

.divPhone {
  display: none;
  justify-content: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  position: sticky;
  flex-direction: column;
  top: 0;
  margin: 0px;
}

.header {
  display: flex;
  justify-content: center;
}

.nav > :hover {
  background: rgb(252, 216, 252);
  color: white;
}

.nav {
  display: flex;
}
.ALMOG {
  display: flex;
  justify-content: center;
  border: 5px solid black;
  border-radius: 10px 10px 10px 10px;
  font-family: fantasy;
  border-top: 10px;
  margin: 0px;
  background: rgb(248, 157, 172);
}

.phone {
  display: flex;
  flex-direction: row;
  margin: 0px;
}

.whatsApp {
  margin: 5px;
}

.facebook {
  margin: 5px;
}

.instegram {
  margin: 5px;
}

.home {
  display: flex;
  padding: 20px;
  text-decoration: none;
  border: 4px solid black;
  border-radius: 100px;
  box-shadow: 5px 5px 5px 5px pink;
  font-size: 20px;
  color: black;
  border-top: 2px;
  height: 50px;
  padding: 10px;
  margin-inline: 15px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   font-style: normal;
}

