@media all and (max-width: 653px) {
  .div {
    background: black;
    display: none;
  }
  .divPhone {
    display: flex;
  }
  .nav {
    flex-direction: column;
  }

  .home {
    justify-content: center;
  }

  .FlowersInBoxese {
    justify-content: center;
  }

  .flowerSlist {
    justify-content: center;
  }

  .BirthdayBows {
    justify-content: center;
  }

  .Balloons {
    text-align: center;
    justify-content: center;
  }

  .Contact {
    justify-content: center;
  }

  .orders {
    justify-content: center;
  }
}
